class Parser {
  /**
   * @param html {string}
   */
  constructor(html) {
    this.html = html;
    this.pos = 0;
  }

  /**
   * @param length {int}
   * @returns {string}
   */
  read(length) {
    const ret = this.html.substring(this.pos, this.pos + length);
    this.pos += length;
    return ret;
  }

  /**
   * @param str {string}
   */
  skipPast(str) {
    while(this.pos < this.html.length && this.read(str.length) !== str) {
      // empty
    }
  }

  parse() {
    const positions = [];

    while(this.pos < this.html.length) {
      const str = this.read(1);

      if(str[0] === '$') {
        if(this.read(1) === '$') {
          this.skipPast('$$');
        } else {
          this.skipPast('$');
        }
      } else if(str === '\\' && this.read(1) === 'p') {
        positions.push(this.pos - 2);
      }
    }

    if(positions.length === 0) {
      return this.html;
    }

    positions.unshift(0);
    positions.push(this.html.length);

    const parts = [];
    for(let i = 0; i < positions.length - 1; i++) {
      let part = this.html.substring(positions[i], positions[i + 1]);

      if(part.startsWith('\\p')) {
        part = part.substring(2);
      }

      parts.push(part);
    }

    let html = parts[0];

    for(let i = 1; i < parts.length; i++) {
      html += '<span class="invisible">' + parts[i] + '</span>';
    }

    return html;
  }
}

export { Parser }
